import React from 'react';
import Title from '../Components/Title';
import ImageSection from '../Components/ImageSection';
import SkillSection from '../Components/SkillSection';
import ServiceSection from '../Components/ServiceSection';
import design from '../img/design.svg';
import intelligence from '../img/intelligence.svg';
import ui from '../img/game-dev.svg';
import {faGithub} from '@fortawesome/free-brands-svg-icons';

function AboutPage() {
    return (
        <div className="AboutPage">
            <Title title={'About Me'} span={'About Me'} />
            <ImageSection />
            <Title title={'My Skills'} span={'My Skills'} />
            <div className="skill-container">
                <SkillSection  skill={'Javascript'} progress={'70%'} width={'70%'} />
                <SkillSection  skill={'React'} progress={'65%'} width={'65%'} />
                <SkillSection  skill={'HTML'} progress={'70%'} width={'70%'} />
                <SkillSection  skill={'CSS'} progress={'60%'} width={'60%'} />
                <SkillSection  skill={'MongoDB'} progress={'50%'} width={'50%'} />
                <SkillSection  skill={'Node JS'} progress={'65%'} width={'65%'} />
            </div>
            <Title title={'Services'} span={'Services'} />
            <div className="service-container">
                <ServiceSection image={design} title={'Web Design'} text={'Can build web applications revolving around personal, business, and organizational sectors.'} />
                <ServiceSection image={intelligence} title={'Integration'} text={'Able to implement any type of internal processing in web applications.'} />
                <ServiceSection image={ui} title={'UI/UX'} text={'Can create user friendly interfaces, that will allow users to stay engaged.'} />
            </div>
        </div>
    )
}

export default AboutPage
