import React from 'react';
import ContactItem from '../Components/ContactItem';
import phone from '../img/phone.svg';
import email from '../img/emailme.svg';
import location from '../img/location.svg';
import Title from '../Components/Title';

function ContactPage() {
    return (
        <div>
            <div className="title">
                <Title title={'Contact Me'} span={'Contact Me'} />
            </div>
            <div className="ContactPage">
            <div className="map-sect">
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d49938.944829397966!2d-122.84555173899325!3d38.52952530931199!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x808417008c262189%3A0x58fe667f522195c2!2sWindsor%2C%20CA!5e0!3m2!1sen!2sus!4v1614598344992!5m2!1sen!2sus" width="600" height="450" style={{border:0}} allowFullScreen="" loading="lazy"></iframe>
            </div>
            <div className='contact-sect'>
                <ContactItem icon={phone} text1={'707-758-2095'} title={'Phone'} />
                <ContactItem icon={email} text1={'BLunaR1337@gmail.com'} title={'Email'} />
                <ContactItem icon={location} text1={'Windsor, CA'} title={'Location'} />
            </div>
        </div>
        </div>
    )
}

export default ContactPage
