import React from 'react';
import about from '../img/about4.jpg';
import prof from '../img/prof.jpg';
import {Link} from 'react-router-dom';

function ImageSection() {
    return (
        <div className="ImageSection">
            <div className="img">
                <img src={prof} alt="" />
            </div>
            <div className="about-info">
                <h4>I am<span> Brandon</span></h4>
                <p className="about-text">
                    Passionate full stack developer looking to further my skillset by
                    taking on complex projects.
                </p>
                <div className="about-details">
                    <div className="left-section">
                        <p>Full Name</p>
                        <p>Age</p>
                        <p>Nationality</p>
                        <p>Country</p>
                    </div>
                    <div className="right-section">
                        <p>: Brandon Luna</p>
                        <p>: 19</p>
                        <p>: Latin</p>
                        <p>: United States</p>
                    </div>
                </div>
                <Link to={{pathname: "https://www.dl.dropboxusercontent.com/s/dzc88wc35p9ctv5/software%20engineering%20resume%20%28college%20undergrad%29.docx?dl=0"}} target="_blank" className="icon-holder">
                <button className="btn">Download Resume</button>
                </Link>
            </div>
        </div>
    )
}

export default ImageSection
